<template>
  <div>
    <!-- 功能按钮 -->
    <div class="MT_fn_btn">
      <el-form :inline="true" :model="queryInfo" ref="ruleForm" class="btn_right demo-form-inline">
        <el-form-item label="" prop="chooseDate">
          <el-date-picker size="small" @change="changeDate" v-model="queryInfo.chooseDate" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="MT_table_cont">
      <!-- 按钮 -->
      <div class="btns">
        <div class="btn_cont">
          <div class="choose_btn _color">
            <span @click.stop="clearAll">批量删除</span>
            <span @click.stop="exportData">导出当前数据</span>
          </div>
        </div>
      </div>
      <!-- 表格列表 -->
      <el-table v-if="path === '/syncResult'" ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column prop="rownum_" label="序号" width="50"> </el-table-column>
        <el-table-column prop="insertTime" label="日期" width="180"> </el-table-column>
        <el-table-column prop="content" label="新增总数"> </el-table-column>
        <el-table-column prop="insertTotal" label="更新总数"> </el-table-column>
        <el-table-column prop="total" label="总数小计"> </el-table-column>
        <el-table-column prop="abatotal" label="废弃总数"> </el-table-column>
        <el-table-column prop="insertSuccess" label="新增成功数"> </el-table-column>
        <el-table-column prop="updateSuccess" label="更新成功数"> </el-table-column>
        <el-table-column prop="totalSuccess" label="成功数小计"> </el-table-column>
        <el-table-column prop="insertFail" label="新增失败数"> </el-table-column>
        <el-table-column prop="updateFail" label="更新失败数"> </el-table-column>
        <el-table-column prop="totalFail" label="失败数小计"> </el-table-column>
      </el-table>

      <!-- 表格列表 -->

      <el-table v-else ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column prop="rownum_" label="序号" width="50"> </el-table-column>
        <el-table-column prop="desc" label="描述"> </el-table-column>
        <el-table-column prop="insertTime" label="日期" width="180"> </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryInfo.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryInfo, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryInfo, changePage)">尾页</span>
        <el-pagination
          background
          :total="queryInfo.total"
          :current-page.sync="queryInfo.page"
          :page-size.sync="queryInfo.size"
          @current-change="changePage"
          layout="total, prev, pager, next, jumper"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['dataInfo', 'queryForm'],
  data() {
    return {
      tableData: [],
      queryInfo: this.queryForm,
      multipleSelection: [],
      path: ''
    }
  },

  methods: {
    // 搜索
    onSubmit() {
      this.$emit('getSearch', this.queryInfo)
    },

    clearAll() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let idList = []
          for (var i = 0; i < this.multipleSelection.length; i++) {
            idList.push(this.multipleSelection[i].id)
          }
          if (this.type === '/syncResult') {
            this.httpPost('/log/sync/result/rem', idList).then(ret => {
              this.$message({ type: 'success', message: '删除成功!' })
              this.$emit('getSearch', this.queryInfo)
            })
          } else {
            this.httpPost('/log/sync/rem', idList).then(ret => {
              this.$message({ type: 'success', message: '删除成功!' })
              this.$emit('getSearch', this.queryInfo)
            })
          }
        })
        .catch(() => {})
    },

    // 页码改变
    changePage() {
      this.$emit('getPage', this.queryInfo.page)
    },

    changeDate(e) {
      if (!e) {
        this.queryInfo.startDate = ''
        this.queryInfo.endDate = ''
      } else {
        this.queryInfo.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryInfo.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      console.log(this.queryInfo)
    },

    // 导出数据
    exportData() {
      let params = Object.assign({}, this.queryInfo)
      delete params.chooseDate
      params.total = ''
      console.log(params)
      let paramArray = Object.keys(params).map(key => key + '=' + params[key])
      console.log(paramArray)
      let url = this.type === '/syncResult' ? '/log/sync/result/export?' : '/log/sync/export?'
      window.open(this.$baseRequestUrl + url + paramArray.join('&'))
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    }
  },

  watch: {
    dataInfo(newVal, oldVal) {
      this.tableData = newVal
    }
  },

  mounted() {
    this.path = this.$route.path
    console.log(this.path)
  }
}
</script>

<style lang="scss" scoped>
.el-date-editor {
  width: 260px;
}
</style>
