<template>
  <div>
    <!-- 功能按钮 -->
    <div class="MT_fn_btn">
      <el-form :inline="true" :model="queryInfo" ref="ruleForm" class="btn_right demo-form-inline">
        <!-- <div class="tips">默认显示30天内数据</div> -->
        <el-form-item label="操作人" prop="userName">
          <el-input v-model="queryForm.userName" placeholder="请输入操作人"></el-input>
        </el-form-item>
        <el-form-item label="动作" prop="content">
          <el-input v-model="queryForm.content" placeholder="请输入查询动作"></el-input>
        </el-form-item>
        <el-form-item label="动作过滤" prop="logType">
          <el-select v-model="queryForm.logType" placeholder="请选择类型">
            <el-option label="登录" value="1"></el-option>
            <el-option label="退出" value="2"></el-option>
            <el-option label="查看" value="3"></el-option>
            <el-option label="下载" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="chooseDate">
          <el-date-picker size="small" @change="changeDate" v-model="queryInfo.chooseDate" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="MT_table_cont">
      <!-- 按钮 -->
      <div class="btns">
        <el-button type="primary" @click.stop="clearAll" icon="el-icon-delete">清除历史日志</el-button>
        <div class="btn_cont">
          <div class="choose_btn _color">
            <span @click.stop="exportData">导出当前数据</span>
          </div>
        </div>
      </div>
      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="rownum_" label="序号" width="50"> </el-table-column>
        <el-table-column prop="userName" label="操作人"> </el-table-column>
        <el-table-column prop="ipAddress" label="IP地址"> </el-table-column>
        <el-table-column label="类型">
          <template slot-scope="scope">
            <span v-if="scope.row.type == 1">登录</span>
            <span v-if="scope.row.type == 2">退出</span>
            <span v-if="scope.row.type == 3">查看</span>
            <span v-if="scope.row.type == 4">下载</span>
          </template>
        </el-table-column>
        <el-table-column prop="content" label="动作"> </el-table-column>
        <el-table-column prop="updateTime" label="时间" width="180"> </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryInfo.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryInfo, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryInfo, changePage)">尾页</span>
        <el-pagination
          background
          :total="queryInfo.total"
          :current-page.sync="queryInfo.page"
          :page-size.sync="queryInfo.size"
          @current-change="changePage"
          layout="total, prev, pager, next, jumper"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['dataInfo', 'queryForm'],
  data() {
    return {
      tableData: [],
      queryInfo: this.queryForm
    }
  },

  methods: {
    // 搜索
    onSubmit() {
      this.$emit('getSearch', this.queryInfo)
    },

    clearAll() {
      this.$confirm('此操作将清除历史数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/log/user/clear', {}).then(res => {
            this.$message.success({ message: '清除成功!' })
          })
        })
        .catch(() => {})
    },

    // 页码改变
    changePage() {
      this.$emit('getPage', this.queryInfo.page)
    },

    changeDate(e) {
      if (!e) {
        this.queryInfo.startDate = ''
        this.queryInfo.endDate = ''
      } else {
        this.queryInfo.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryInfo.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      console.log(this.queryInfo)
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    // 导出数据
    exportData() {
      let params = Object.assign({}, this.queryInfo)
      delete params.chooseDate
      params.total = ''
      console.log(params)
      let paramArray = Object.keys(params).map(key => key + '=' + params[key])
      console.log(paramArray)
      window.open(this.$baseRequestUrl + '/log/user/export?' + paramArray.join('&'))
    }
  },

  watch: {
    dataInfo(newVal, oldVal) {
      this.tableData = newVal
    }
  },
  mounted() {}
}
</script>

<style lang="scss" scoped>
.el-date-editor {
  width: 260px;
}
/deep/.el-table td,
.el-table th {
  padding: 4px 0;
}
</style>
