<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>日志管理</el-breadcrumb-item>
        <el-breadcrumb-item>系统日志</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!-- 管理员日志 -->
        <el-tab-pane label="管理员日志" name="Admin">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" />
        </el-tab-pane>

        <!-- 用户日志 -->

        <el-tab-pane label="用户日志" name="user">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import TableData from './SystemTableData'
export default {
  components: { TableData },
  data() {
    return {
      activeName: 'Admin',

      queryForm: {
        userName: '', // 用户名操作人
        content: '', // 日志动作
        userType: 0, // 0管理员 1用户
        chooseDate: '',
        startDate: '',
        endDate: '',
        size: 20,
        page: 1,
        logType: '', // 1登录 2退出 3查看 4下载
        total: ''
      },
      dataInfo: []
    }
  },
  methods: {
    getPage(e) {
      this.queryForm.page = e
      this.loadData()
    },

    getSearch(e) {
      this.queryForm = e
      this.loadData()
    },

    loadData(type) {
      this.queryForm.total = ''
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate
      this.httpGet('/log/user/query', params, '', type).then(res => {
        this.dataInfo = res.rows
        this.queryForm.total = res.total
      })
    },

    handleClick(tab, event) {
      this.queryForm.page = 1
      this.queryForm.total = ''
      console.log(this.activeName)
      if (this.activeName === 'Admin') {
        this.queryForm.userType = 0
        this.loadData(1)
      } else {
        this.queryForm.userType = 1
        this.loadData(1)
      }
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
