<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>日志管理</el-breadcrumb-item>
        <el-breadcrumb-item>同步结果日志</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!-- 组织机构代码同步 -->
        <el-tab-pane label="组织机构代码同步" name="code">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" />
        </el-tab-pane>

        <!-- 标准题录库同步 -->
        <el-tab-pane label="标准题录库同步" name="tiluku">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" />
        </el-tab-pane>

        <!-- 文本关联 -->
        <el-tab-pane label="文本关联" name="text">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" />
        </el-tab-pane>

        <!-- 欧盟数据同步 -->
        <el-tab-pane label="欧盟数据同步" name="eudata">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" />
        </el-tab-pane>

        <!-- 水印文本复制 -->
        <el-tab-pane label="水印文本复制" name="watermark">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import TableData from './SyncResultTable'
export default {
  components: { TableData },
  data() {
    return {
      activeName: 'code',

      queryForm: {
        chooseDate: '',
        startDate: '',
        endDate: '',
        size: 10,
        page: 1,
        queryType: 1, // 1用户同步 2题录库 3文本关联 4欧盟 5水印
        total: ''
      },
      dataInfo: []
    }
  },
  methods: {
    getPage(e) {
      this.queryForm.page = e
      this.loadData()
    },

    getSearch(e) {
      this.queryForm = e
      this.loadData()
    },

    loadData(type) {
      this.queryForm.total = ''
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate
      this.httpGet('/log/sync/result/query', params, '', type).then(res => {
        this.dataInfo = res.rows
        this.queryForm.total = res.total
      })
    },

    handleClick(tab, event) {
      this.queryForm.page = 1
      this.queryForm.total = ''
      console.log(this.activeName)
      switch (this.activeName) {
        case 'code':
          this.queryForm.queryType = 1
          break
        case 'tiluku':
          this.queryForm.queryType = 2
          break
        case 'text':
          this.queryForm.queryType = 3
          break
        case 'eudata':
          this.queryForm.queryType = 4
          break
        default:
          this.queryForm.queryType = 5
          break
      }
      this.loadData(1)
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
